import React from 'react';
import { Link } from 'react-router-dom';
import { useState, useEffect } from 'react';
import '../layouts/chapter.css';
import '../layouts/font-awesome.css';
import { PYODIDE_URL, PYODIDE_INDEX_URL } from '../constants/pyodide';
import {
  PYTHON_PANDAS_QUERY,
  PYTHON_SIMPLE_BAR_GRAPH,
  PYTHON_SIMPLE_LINE_GRAPH,
  PYTHON_SIMPLE_PIE_GRAPH
} from '../contents/codeSnips';
import contents from '../contents/contentSnips';
import AceEditor from 'react-ace';
import 'ace-builds/src-noconflict/mode-python';
import 'ace-builds/src-noconflict/theme-github';
import 'ace-builds/src-noconflict/ext-language_tools';

function Datascience() {
  const [pythonPandasQuery, setPythonPandasQuery] = useState(PYTHON_PANDAS_QUERY);
  const [pythonBarGraph, setPythonBarGraph] = useState(PYTHON_SIMPLE_BAR_GRAPH);
  const [pythonLineGraph, setPythonLineGraph] = useState(PYTHON_SIMPLE_LINE_GRAPH);
  const [pythonPieGraph, setPythonPieGraph] = useState(PYTHON_SIMPLE_PIE_GRAPH);
  const [output, setOutput] = useState(null);
  const [output1, setOutput1] = useState(null);
  const [output2, setOutput2] = useState(null);
  const [output3, setOutput3] = useState(null);
  const maxLinesAce = 50;
  useEffect(() => {
    const script = document.createElement('script');
    script.src = PYODIDE_URL;
    script.async = true;
    document.body.appendChild(script);
    return () => {
      document.body.removeChild(script);
    }
  }, []);
  const runPydide = async (scriptText) => {
    const pyodide = await window.loadPyodide({
      indexURL: PYODIDE_INDEX_URL
    });
    const out = await pyodide.runPythonAsync(scriptText);
    return (out);
  };
  const runPythonPandasQuery = async () => {
    setOutput('Running...');
    const scriptText = pythonPandasQuery;
    const out = await runPydide(scriptText);
    setOutput(out);
  };
  const runPythonBarGraph = async () => {
    setOutput1('Running...');
    const scriptText = pythonBarGraph;
    const out = await runPydide(scriptText);
    setOutput1(out);
  };
  const runPythonLineGraph = async () => {
    setOutput2('Running...');
    const scriptText = pythonLineGraph;
    const out = await runPydide(scriptText);
    setOutput2(out);
  };
  const runPythonPieGraph = async () => {
    setOutput3('Running...');
    const scriptText = pythonPieGraph;
    const out = await runPydide(scriptText);
    setOutput3(out);
  };
  return (
    <div id="content" className="content">
      <main>
        <h1 id="introduction-to-pandas"><a className="header" href="#introduction-to-pandas">{contents.chapter2.HEADING_4}</a></h1>
        <p>{contents.chapter2.CONTENT_11}</p>
        <p>{contents.chapter2.CONTENT_12}</p>
        <div className="divStyle">
          <button onClick={runPythonPandasQuery} className="buttonStyle fa fa-play play-button"></button>
        </div>
        <AceEditor
          mode="python"
          id="pythonPandasQuery"
          theme="github"
          value={pythonPandasQuery}
          maxLines={maxLinesAce}
          className="w-100"
          onChange={(value) => setPythonPandasQuery(value)}
          name="pythonPandasQuery"
          editorProps={{ $blockScrolling: true }}
        />
        {output && <textarea
          name=""
          id="txt"
          className="resultTextareaStyle w-100"
          value={output}
          cols="91"
        ></textarea>}

        <h1 id="visualizing-data-using-matplotlib"><a className="header" href="#visualizing-data-using-matplotlib">{contents.chapter2.HEADING_5}</a></h1>
        <p>{contents.chapter2.CONTENT_13}</p>
        <div className="divStyle">
          <button onClick={runPythonBarGraph} className="buttonStyle fa fa-play play-button"></button>
        </div>
        <AceEditor
          mode="python"
          id="pythonPandasQuery"
          theme="github"
          value={pythonBarGraph}
          maxLines={maxLinesAce}
          className="w-100"
          onChange={(value) => setPythonBarGraph(value)}
          name="pythonBarGraph"
          editorProps={{ $blockScrolling: true }}
        />
        {output1 && <textarea
          name=""
          id="txt"
          className="resultTextareaStyle w-100"
          value={output1}
          cols="91"
        ></textarea>}
        <p>{contents.chapter2.CONTENT_14}</p>
        <div className="divStyle">
          <button onClick={runPythonLineGraph} className="buttonStyle fa fa-play play-button"></button>
        </div>
        <AceEditor
          mode="python"
          id="pythonPandasQuery"
          theme="github"
          value={pythonLineGraph}
          maxLines={maxLinesAce}
          className="w-100"
          onChange={(value) => setPythonLineGraph(value)}
          name="pythonLineGraph"
          editorProps={{ $blockScrolling: true }}
        />
        {output2 && <textarea
          name=""
          id="txt"
          className="resultTextareaStyle w-100"
          value={output2}
          cols="91"
        ></textarea>}

        <p>{contents.chapter2.CONTENT_15}</p>
        <div className="divStyle">
          <button onClick={runPythonPieGraph} className="buttonStyle fa fa-play play-button"></button>
        </div>
        <AceEditor
          mode="python"
          id="pythonPandasQuery"
          theme="github"
          value={pythonPieGraph}
          maxLines={maxLinesAce}
          className="w-100"
          onChange={(value) => setPythonPieGraph(value)}
          name="pythonPieGraph"
          editorProps={{ $blockScrolling: true }}
        />
        {output3 && <textarea
          name=""
          id="txt"
          className="resultTextareaStyle w-100"
          value={output3}
          cols="91"
        ></textarea>}
      </main>
      <nav className="nav-wrapper" aria-label="Page navigation">
        <Link to="/chapter3" className="nav-chapters next">
          <i className="fa fa-angle-right"></i>
        </Link>
        <Link to="/chapter1" className="nav-chapters previous">
          <i className="fa fa-angle-left"></i>
        </Link>
      </nav>
    </div>
  );
}

export default Datascience;
